.portfolio-page {
    padding-left: 100px;
    padding-right: 50px;
    width: calc(100% - 150px);
    position: initial;
    height: 100%;
    overflow: auto;

    h1 {
        font-size: 4em;
        font-family: 'Coolvetica';
        color: #FD7013;
        font-weight: 400;
        margin-top: 0;
        position: relative;
        margin-bottom: 40px;
        right: 100px;

        &:before {
            content: '<h1>';
            font-family: 'La Belle Aurore', cursive;
            color: #FD7013;
            font-size: 18px;
            position: absolute;
            margin-top: -10px;
            left: -10px;
            opacity: 0.6;
            line-height: 18px;
        }

        &:after {
            content: '</h1>';
            font-family: 'La Belle Aurore', cursive;
            color: #FD7013;
            font-size: 18px;
            line-height: 18px;
            position: absolute;
            left: -30px;
            bottom: -20px;
            margin-left: 20px;
            opacity: 0.6;
        }
    }

    h1.page-title {
        margin-left: 100px;
        margin-top: 100px;
    }

    .images-container {
        //display: flex;
        margin-bottom: 30em;
        //flex-wrap: wrap;
        padding-bottom: 100px;
    }

    .image-box {
        position: relative;
        flex: 1 1 20%;
        height: 30em;
        margin-bottom: 8em;
        border-radius: 10px;

        .portfolio-image {
            position: absolute;
            z-index: 2;
            width: 23.6em;
            height: 100%;
            object-fit: cover;
            margin-bottom: 30px;
        }

        .content {
            position: absolute;
            width: 20em;
            z-index: 3;
            padding: 10px 20px;
            transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
            background: linear-gradient(180deg,
                    rgba(0, 0, 0, 0.2) 0,
                    rgba(0, 0, 0, 1));
            bottom: -70px;
        }

        .title {
            margin-bottom: 0;
            margin-top: 0;
            color: #fff;
            font-size: 2rem;
            font-weight: 500;
            line-height: 24px;
        }

        .description {
            font-size: 1rem;
            margin-bottom: 5px;
            color: #fff;
            font-weight: 700;
        }

        .btn {
            margin-top: 30px;
            margin-bottom: 10px;
            padding: 0 23px;
            height: 40px;
            line-height: 34px;
            border: 2px solid #FD7013;
            border-radius: 4px;
            font-size: 14px;
            color: #fff;
            background: transparent;
            text-transform: uppercase;
            font-weight: 700;
            transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
            cursor: pointer;
        }

        .btn:hover {
            transform: translateY(-3px);
            background: #FD7013;
        }

        &:after {
            content: "";
            background: linear-gradient(180deg, #FD7013, #000);
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            width: 100%;
            height: 100%;
            z-index: 2;
            transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
            opacity: 0;
        }

        &:hover:after {
            opacity: 0.85;
        }

        &:hover .content {
            bottom: 0;
            background: transparent;
        }
    }
}

